@import url(https://fonts.googleapis.com/css?family=Dancing+Script&display=swap);
*{box-sizing:border-box}a{text-decoration:none}nav a{color:white}nav a:hover{background:#167be0}nav button{color:white}body{margin:0;background:#FAFAFA;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.app{font-family:'Nanum Gothic', sans-serif;overflow:hidden}.app main{margin-top:60px;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.app footer{display:flex;justify-content:center;align-items:center;width:100%;height:30px;color:#6E6E6E}table{width:90%;border-collapse:collapse;table-layout:fixed}table td,table th{text-overflow:ellipsis;overflow:hidden;white-space:nowrap}

header{position:fixed;left:0;top:0;width:100%;z-index:1000}header .navigation{display:flex;justify-content:flex-start;align-items:center;width:100%;height:60px;background:#1976d2;box-shadow:0 2px 5px 0 rgba(0,0,0,0.3)}header .navigation .logo{display:flex;justify-content:center;align-items:center;width:auto;height:60px;padding:0px 10px;font-family:'Dancing Script', cursive}header .navigation .logo a{display:flex;justify-content:center;align-items:center;width:auto;height:40px;border-radius:3px;padding:0px 20px;font-size:30px}header .navigation .menus{display:flex;justify-content:center;align-items:center}header .navigation .menus .menu{display:flex;justify-content:center;align-items:center;width:auto;height:60px;padding:0px 10px}header .navigation .menus .menu a{display:flex;justify-content:center;align-items:center;width:auto;height:40px;border-radius:3px;padding:0px 20px}header .navigation .login-menus{position:absolute;right:0;top:0;display:flex;justify-content:center;align-items:center;width:auto;height:60px}header .navigation .login-menus .login-menu{display:flex;justify-content:center;align-items:center;width:auto;height:40px;padding:0px 20px;border:none;border-radius:3px;background:none}header .navigation .login-menus .login-menu a{display:flex;justify-content:center;align-items:center;width:auto;height:40px;border-radius:3px;padding:0px 20px}header .navigation .login-menus .login-menu .ri-list-settings-fill{color:white;font-size:30px}header .navigation .login-menus .log-drop{display:block;width:150px;height:40px;border:none;border-radius:3px;background:none}header .navigation .login-menus .log-drop div{display:flex;justify-content:center;align-items:center;width:100%;height:40px;color:white}header .navigation .login-menus .log-drop .logout-wrap{display:none;background:white;border:1px solid gray;border-radius:3px;color:black}header .navigation .login-menus .log-drop .logout-wrap div{color:black;cursor:pointer}header .navigation .login-menus .log-drop:hover .logout-wrap{display:flex}@media (min-width: 501px) and (max-width: 1000px){header .navigation{justify-content:center}header .navigation .menus{display:none}header .navigation .login-menus{display:none}}@media all and (max-width: 500px){header .navigation{justify-content:center}header .navigation .menus{display:none}header .navigation .login-menus{display:none}}

a{color:#585858}.menu-btn{position:absolute;left:0;top:0;width:auto;height:60px;display:none;justify-content:center;align-items:center}.menu-btn button i{color:white;font-size:30px}@media (min-width: 501px) and (max-width: 1000px){.menu-btn{display:flex}}@media all and (max-width: 500px){.menu-btn{display:flex}}

.content-wrap{width:600px;border-radius:2px;margin-top:10px;margin-bottom:5px;box-shadow:0 5px 10px rgba(154,160,185,0.05),0 15px 40px rgba(166,173,201,0.2);color:#585858;z-index:50px;background:white}.content-wrap:last-child{margin-bottom:40px}.content-wrap .top-wrap{display:flex;justify-content:flex-start;align-items:center;border-bottom:1.5px solid #F2F2F2;padding:10px;position:relative}.content-wrap .top-wrap .title-wrap{display:flex;justify-content:flex-start;align-items:center;width:250px}.content-wrap .top-wrap .title-wrap i{font-size:25px}.content-wrap .top-wrap .title-wrap span{font-size:20px}.content-wrap .top-wrap .category-wrap{position:absolute;right:0;top:0;display:flex;justify-content:flex-end;align-items:center;width:auto;height:100%;padding:0px 15px}.content-wrap .top-wrap .category-wrap a{display:flex;justify-content:flex-end;align-items:center;font-size:16px;color:#0489B1}.content-wrap .top-wrap .category-wrap i{color:#0489B1}.content-wrap .date-wrap{display:flex;justify-content:flex-start;align-items:center;padding:10px}.content-wrap .comment-wrap{padding:12px;line-height:1.3em}.img-wrap{display:flex;justify-content:center;align-items:center;width:100%;margin-top:5px;margin-bottom:15px}.img-wrap img{width:95%;border-radius:3px}.video-wrap{display:flex;justify-content:center;align-items:center;margin-bottom:15px}.video-wrap video{border-radius:2px}.content-wrap .link-wrap{margin-top:10px}.content-wrap .link-wrap span{border-radius:3px;color:#58ACFA}.content-wrap .link-wrap span a{color:#58ACFA}.content-wrap .bottom-wrap{padding:10px}.content-wrap .bottom-wrap i{font-size:25px;margin-right:20px}.content-wrap .bottom-wrap .ri-heart-fill,.ri-heart-add-fill{color:#F78181;cursor:pointer}.content-wrap .bottom-wrap .ri-download-cloud-fill{color:#F7BE81;cursor:pointer}@media (min-width: 501px) and (max-width: 1000px){.content-wrap{width:500px}}@media all and (max-width: 500px){.content-wrap{width:100%}.content-wrap .comment-wrap .img-wrap img{width:98%;border-radius:2px}}.loading-wrap{display:flex;flex-direction:column;justify-content:center;align-items:center;width:auto;height:300px;line-height:1.5em}

.total-back-wrap{display:flex;justify-content:center;align-items:center;width:600px;background:white;border-radius:3px;padding:20px 5px;box-shadow:0 5px 10px rgba(154,160,185,0.05),0 15px 40px rgba(166,173,201,0.2);color:#585858;margin-top:30px;margin-bottom:10px}.total-back-wrap .total-wrap{flex:1 1;display:flex;flex-direction:column;justify-content:center;align-items:center}.total-back-wrap .total-wrap p{font-size:25px}.total-back-wrap .total-wrap h1{margin-top:5px}@media (min-width: 501px) and (max-width: 1000px){.total-back-wrap{width:500px}}@media all and (max-width: 500px){.total-back-wrap{width:100%;margin-top:0px}.total-back-wrap h1{font-size:10px}}

.explanation-wrap{display:flex;flex-direction:column;justify-content:center;align-items:center;width:600px;background:white;border-radius:2px;padding:20px;box-shadow:0 5px 10px rgba(154,160,185,0.05),0 15px 40px rgba(166,173,201,0.2);line-height:1.3em;margin-top:30px;margin-bottom:10px;color:#585858}.explanation-wrap span{padding:0px 10px;font-size:18px;font-weight:600px;line-height:2em}@media (min-width: 501px) and (max-width: 1000px){.explanation-wrap{width:500px}}@media all and (max-width: 500px){.explanation-wrap{width:100%;margin-top:0px}}

form{display:flex;flex-direction:column;justify-content:center;align-items:flex-start;margin:40px}form .formName{display:flex;justify-content:center;align-items:center;width:100%;margin-top:5px;margin-bottom:10px;font-size:20px}form .img-wrap{display:flex;justify-content:center;align-items:center;width:500px}form .img-wrap img{width:50%;border-radius:3px}form select{width:300px;height:30px;border:1px solid gray;border-radius:3px;background:white;margin-top:5px;margin-bottom:10px}form input{width:500px;height:40px;border:1px solid gray;border-radius:3px;background:white;margin-top:5px;margin-bottom:10px;padding-left:10px}form textarea{width:500px;border:1px solid gray;border-radius:3px;background:white;margin-top:5px;margin-bottom:10px}form .button-wrap{display:flex;justify-content:center;align-items:center;width:100%;margin-top:10px}form .log-state{display:flex;justify-content:center;align-items:center;width:100%;margin:5px 0px;color:red}@media (min-width: 501px) and (max-width: 1000px){.navigation{justify-content:center}.navigation .menus{display:none}.navigation .login-menus{display:none}}@media all and (max-width: 500px){form input{width:300px}form textarea{width:300px}form select{width:200px}}

